import React from 'react';
import AbstractList from '../Core/AbstractList'


class RawMaterial extends AbstractList {

	constructor( props ) {
		super( props );

		this.getCreatePanel = this.getCreatePanel.bind( this );
	}

	getEntityName() {
		return "rawMaterial";
	}

	getSearchPanel() {
		return [
			{ id: "LIKE:title", type: 'text', label: '' },
			{ id: "EQ:category.id", type: 'combo', label: '  ',
				values: this.state.options ? this.state.options.categories : [],
				convertToVal: it => it.id, convertToStr: it => it.title },
			{ id: "EQ:deleted", type: 'combo', label: ' ', value: false,
				values: [ { id: true, title: '' }, { id: false, title: '' }],
				convertToVal: it => it.id, convertToStr: it => it.title }
		]
	}

	getTableHead() {
		return ["", "" , "", " ", ""];
	}

	getCreatePanel() {
		let value = ( this.state.data && this.state.data.category ? this.state.data.category.id : undefined );
		return [
			{ id: "title", type: 'text', label: '' },
			{ id: "unitPrice", type: 'number', label: ' ' },
			{ id: "category", type: "combo", label: '', value: value, values: this.state.options ? this.state.options.categories : [],
				convertToVal: it => it.id , convertToStr: it => it.title }
		];
	}


	saveEntity(entity) {
		this.refineComboBox( "category", entity );
		super.saveEntity( entity );
	}


	convertToTableRow( data ) {
		return (
			<tr key={"item_" + data.id}>
				<td>{data.id}</td>
				<td>{data.title}</td>
				<td>{ data.category ? data.category.title : "" }</td>
				<td>{data.unitPrice}</td>
				<td>
					{this.createControleIcons(data)}
				</td>
			</tr>
		);
	}

}


export default RawMaterial;


